import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import "../style/tictactoe.scss";
const initMap = [
  [null, null, null],
  [null, null, null],
  [null, null, null]
];
export default function Div() {
  let [mapArray, setMapArray] = useState(initMap);
  let [isWinTarget, setIsWinTarget] = useState(false);
  let [n, setN] = useState(0);
  let [whichWin, setWhichWinTarget] = useState(null);
  function onClickFns(data1, data2, ev) {
    let newMapArray = JSON.parse(JSON.stringify(mapArray));
    if (newMapArray[data1][data2]) return alert("不可重复点击");
    newMapArray[data1][data2] = n % 2 ? "x" : "o";
    setN(n + 1);
    console.log(newMapArray);
    setMapArray(newMapArray);
    if (isWin(newMapArray)) {
      console.log("win", newMapArray[data1][data2]);
      setWhichWinTarget(newMapArray[data1][data2]);
      setIsWinTarget(true);
    }
  }
  function isWin(array) {
    for (let index = 0; index < array.length; index++) {
      const element = array[index];
      if (
        element[0] === element[1] &&
        element[1] === element[2] &&
        element[2] !== null
      ) {
        console.log("error 2");
        return true;
      }
      if (
        array[0][index] === array[1][index] &&
        array[1][index] === array[2][index] &&
        array[2][index] != null
      ) {
        console.log("error x");

        return true;
      }
      if (
        array[0][0] === array[1][1] &&
        array[1][1] === array[2][2] &&
        array[2][2] != null
      ) {
        console.log("error 3");
        return true;
      }
      if (
        array[0][2] === array[1][1] &&
        array[1][1] === array[2][0] &&
        array[2][0] != null
      ) {
        console.log("error 4");
        return true;
      }
      console.log([...array].includes(null));
      const newArray = [];
      for (let index = 0; index < array.length; index++) {
        const element = array[index];
        newArray.concat(...newArray, [...element]);
      }
      console.log(69, newArray);
      console.log(70, newArray.includes(null));
    }
  }
  function Cell(props) {
    return (
      <div className="cell" onClick={props.onClickFn}>
        {props.content}
      </div>
    );
  }
  function closeWrap() {
    console.log("关闭赢了的wrap");
    setIsWinTarget(false);
    setMapArray(initMap);
  }
  function clickP(event) {
    event.stopPropagation();
  }
  function ShowMap() {
    return (
      <div className="cellMap">
        {mapArray.map((items, row) => (
          <div className="row" key={row}>
            {items.map((item, col) => (
              <Cell
                key={row + col}
                onClickFn={ev => onClickFns(row, col, ev)}
                content={item}
              />
            ))}
          </div>
        ))}
        {isWinTarget && (
          <div className="isWin" onClick={closeWrap}>
            <p onClick={clickP}>{whichWin}赢了</p>
          </div>
        )}
      </div>
    );
  }
  function BackIndex() {
    let history = useHistory();
    function handleClick() {
      history.push("/");
    }
    return (
      <div className="back-button" onClick={handleClick}>
        返回
      </div>
    );
  }

  return (
    <div className="mapWrap">
      <h1>井字棋游戏</h1>
      <BackIndex />
      <ShowMap />
    </div>
  );
}
